<!-- 消息列表 -->
<template>
  <div class="message">
    <div class="top-fixed">
      <div class="search" @click="searchClick">搜索</div>
    </div>
    <div class="content">
      <div class="list-item" v-for="(item, index) in topList" :key="item.moduleid">
        <i class="nail"></i>
        <van-swipe-cell @click.native="toChat(item.moduleid, item.modulename, item.icon)">
          <div class="item">
            <div class="img">
              <img :src="item.icon" alt="">
            </div>
            <div class="text">
              <div class="title">
                <h3>{{item.modulename}}</h3>
                <span>{{item.newest_time | timeFilter}}</span>
              </div>
              <div class="msg">
                <p>{{item.newest_msg}}</p>
                <span class="count" v-if="item.count">{{item.count}}</span>
              </div>
            </div>
          </div>
          <template #right>
            <van-button class="top" @click="cancelTopHandle(index, item.moduleid, item.modulename)" square :text="'取消置顶'" />
            <van-button class="delete" @click="deleteHandle(index, item.moduleid, true)" square text="删除" />
          </template>
        </van-swipe-cell>
      </div>
      <div class="list-item" v-for="(item, index) in list" :key="item.moduleid">
        <van-swipe-cell @click.native="toChat(item.moduleid, item.modulename, item.icon)">
          <div class="item">
            <div class="img">
              <img :src="item.icon" alt="">
            </div>
            <div class="text">
              <div class="title">
                <h3>{{item.modulename}}</h3>
                <span>{{item.newest_time | timeFilter}}</span>
              </div>
              <div class="msg">
                <p>{{item.newest_msg}}</p>
                <span class="count" v-if="item.count">{{item.count}}</span>
              </div>
            </div>
          </div>
          <template #right>
            <van-button class="top" @click="topHandle(index, item.moduleid, item.modulename)" square :text="'置顶'" />
            <van-button class="delete" @click="deleteHandle(index, item.moduleid)" square text="删除" />
          </template>
        </van-swipe-cell>
      </div>
    </div>
    <tabbar></tabbar>
  </div>
</template>

<script>
import { Toast } from 'vant';
import axios from 'axios';
const productionUrl = window.origin + '/mobile456/xxtx/py';
const instance = axios.create({
  baseURL: process.env.NODE_ENV === 'development' ? '/api/mobile' : productionUrl
});
export default {
  name: "Message",
  components: {
    Tabbar: () => import("../../components/Tabbar.vue")
  },
  data() {
    return {
      topList: [],
      list: [],
    };
  },
  created() {},
  mounted() {
    this.browseRecord() //浏览记录
    this.getList();
  },
  methods: {
    //浏览记录
    browseRecord(){
      this.$axios.post("/hopeUserLog_h/insertFourCh",{
        // aamid:"1",
        "aamid": this.$store.state.userInfoObj.aamid,
        "moduleid":-2,
        "logtime":new Date()
      })
    },
    getList() {
      instance.post('/msg-lst', {
        aamid: this.$store.state.userInfoObj.aamid,
        moduleidList: this.$store.state.moduleidList
      }).then(res => {
				if (res.data.message === "OK" && res.status === 200) {
          const data = res.data.data || [];
          data.forEach(item => {
            item.istop === 1 ? this.topList.push(item) : this.list.push(item);
          });
				}
      });
    },
    searchClick() {
      this.$router.push({name: 'MessageSearch'});
    },
    // 取消置顶
    cancelTopHandle(index, id, name) {
      instance.post(`/istop`, {
          "aamid": this.$store.state.userInfoObj.aamid,
					// "aamid": "888803996",
          "moduleid": id,
          "modulename": name
				}).then(res => {
				if (res.data.errcode === "success" && res.status === 200) {
          const item = this.topList.splice(index, 1);
          this.list.push(item[0]);
				} else {
          Toast.fail('请重试');
        }
			});
    },
    // 置顶
    topHandle(index, id, name) {
      instance.post(`/istop`, {
          "aamid": this.$store.state.userInfoObj.aamid,
					// "aamid": "123",
          "moduleid": id,
          "modulename": name
				}).then(res => {
				if (res.data.errcode === "success" && res.status === 200) {
          const item = this.list.splice(index, 1);
          this.topList.unshift(item[0]);
				} else {
          Toast.fail('请重试');
        }
			});
    },
    deleteHandle(index, id, isTop) {
      instance.post(`/del-msg`, {
          "aamid": this.$store.state.userInfoObj.aamid,
					// "aamid": "111",
					"moduleid": id
				}).then(res => {
				if (res.data.errcode === "success" && res.status === 200) {
          isTop ? this.topList.splice(index, 1) : this.list.splice(index, 1);
          // localStorage.removeItem(`${id}&${this.$store.state.userInfoObj.aamid}`);
				} else {
          Toast.fail('请重试');
        }
			});
    },
    toChat(moduleid, modulename, icon) {
      const aamid = this.$store.state.userInfoObj.aamid;
      // window.location.href = `${productionUrl}/xxtx/py/chat?moduleid=${moduleid}&modulename=${modulename}&moduleIcon=${icon.split('//')[1]}&aamid=${aamid}`;
      this.$router.push({name: 'MessageChat', params: { moduleid, modulename, icon }});
    },
  },
  filters: {
    timeFilter(value) {
      return value ? value.substring(0, 16) : value;
    }
  },
};
</script>
<style lang='less' scoped>
/* @import url(); 引入css类 */
.message{
  .top-fixed {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    height: 0.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid #EAEAEA;
    padding: 0 0.1rem;
    background: #fff;
    box-sizing: border-box;
    .search{
      flex: 1;
      box-sizing: border-box;
      background: #F1F1F1 url('../../assets/images/icon_search@2x.png') no-repeat 0.12rem center;
      background-size: 0.2rem 0.2rem;
      border-radius: 0.15rem;
      height: 0.3rem;
      line-height: 0.2rem;
      font-size: 0.14rem;
      color: #000;
      text-indent: 0.4rem;
      font-size: 0.14rem;
      color: #A6A6A6;
      line-height: 0.3rem;
    }
  }
  .content{
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    .list-item{
      position: relative;
      .nail{
        position: absolute;
        border: 0.06rem solid #fff;
        border-top: 0.06rem solid #45A4B1;
        border-left: 0.06rem solid #45A4B1;
        z-index: 2;
      }
    }
    .item{
      height: 0.7rem;
      background: #fff;
      display: flex;
      .img{
        width: 0.74rem;
        height: 100%;
        img{
          width: 0.46rem;
          height: 0.46rem;
          margin: 0.12rem 0 0 0.16rem;
        }
      }
      .text{
        padding-top: 0.14rem;
        padding-right: 0.12rem;
        flex: 1;
        border-bottom: 1px solid #EAEAEA;
        .title{
          height: 0.2rem;
          line-height: 0.2rem;
          font-size: 0.15rem;
          color: #3A3A3A;
          display: flex;
          margin-bottom: 0.07rem;
          h3{
            flex: 1;
            overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
          }
          span{
            font-size: 0.12rem;
            color: #969696;
          }
        }
        .msg{
          position: relative;
          
          p{
            width: 2.3rem;
            overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
            font-size: 0.12rem;
            color: #969696;
            line-height: 0.18rem;
          }
          .count{
            height: 0.14rem;
            background: #EB5A55;
            border-radius: 0.07rem;
            padding: 0 0.04rem;
            color: #fff;
            font-size: 0.12rem;
            position: absolute;
            right: 0;
            top: 0.02rem;
            line-height: 0.14rem;
          }
        }
      }
    }
    .top, .delete{
      height: 100%;
      width: 0.78rem;
      color: #fff;
      background: #C8C7CD;
      text-align: center;
      line-height: 0.7rem;
      font-size: 0.15rem;
      border: 0;
      padding: 0;
    }
    .delete{
      background: #EB5A55;
      margin-left: 2px;
    }
  }
}
</style>